.App {
  text-align: center;
}

.all-page {
}

.App-logo {
  height: 20vmin;
  pointer-events: none;
  padding: 70px;
  border-radius: 120px;
}

.store-button {
  height: 5vmin;
  pointer-events: none;
  padding: 20px;
}

.button {
  height: 5vmin;
  width: 20vmin;
  margin: 5vmin;
  background-color: #4877e3;
  font-size: 20px;
  font-weight: bold;
  border-radius: 50px;
  outline-color: blue;
  border: none;
}

.store-button-container {
  display: flex;
  justify-content: space-between;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
